<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import { formatDate } from "@/util";
import { tips } from "@/util/tips";
import mySvg from "@/assets/images/YIFANDOU.svg";
import bg2 from "@/assets/images/bg2.jpg";

export default {
  name: "VoiceReg",
  data() {
    return {
      isXialaClick: false,
      isFixed: false,
      isFill: false,
      isALlFillComplete: false,
      backgroundImg: null,
      sections: [
        "Profile",
        "Experiences",
        "Publications",
        "Projects",
        "Teaching",
        "Message",
        "Contact"
      ]
    };
  },
  watch: {},
  computed: {},
  beforeRouteLeave(to, from, next) {
    next();
  },
  mounted() {
    var img = new Image();
    img.onload = _ => {
      this.backgroundImg = bg2;
      var svg = new Vivus(
        "username",
        {
          file: this.mySvg,
          duration: 2400
        },
        _ => {
          this.isFill = true;
          setTimeout(_ => {
            this.isALlFillComplete = true;
          }, 1400);
        }
      );
    };
    img.src = bg2;
  },
  created() {
    window.addEventListener("scroll", this.scrolling, false);
    this.mySvg = mySvg;
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrolling, false);
  },
  methods: {
    xiala() {
      this.jumpTo("Profile");
      this.isXialaClick = true;
    },
    jumpTo(title) {
      var ele = this.$refs[title];
      var top = ele.offsetTop;
      window.scrollTo({ top: top, left: 0, behavior: "smooth" });
    },
    scrolling(e) {
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      this.isFixed = (scrollTop > document.documentElement.clientHeight) && document.documentElement.clientWidth > 1200;
    }
  }
};
</script>

